.coner-box .box-top,
.coner-box .box-middle,
.coner-box .box-bottom {
  padding: 0 .21rem;
}
.coner-box .box-top {
  width: 100%;
  height: 1rem;
  background: url(../images/box_top_pic.png) no-repeat center top -0.1rem;
  background-size: 100% 1.1rem;
}
.coner-box .box-top .head {
  margin-top: .05rem;
}
.coner-box .box-middle {
  min-height: 300px;
  background: url(../images/box_middle_pic.png) repeat-y;
  background-size: 100% .01rem;
}
.coner-box .box-bottom {
  height: .38rem;
  background: url(../images/box-bottom_pic.png) no-repeat center top;
  background-size: 100% .5rem;
}
.coner-box .head-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.coner-box .head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
  padding-left: .34rem;
  line-height: .78rem;
}
.coner-box .head span i {
  margin-right: .18rem;
  font-size: .3rem;
  color: #F3Af1B;
}
.coner-box .head i {
  font-size: .24rem;
  margin-right: .28rem;
  color: #ffffff;
  cursor: pointer;
}
.beforeAfter,
.dot-line-list li .title:before,
.dot-line-list li::before,
.dot-line-list li::after {
  position: absolute;
  width: 0;
  height: 0;
  content: '';
}
.dl-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #81A0D3;
}
.dl-info dt span {
  margin-right: .1rem;
}
.dl-info dd span {
  margin-left: .1rem;
}
.dl-info .no-read {
  color: #F3Af1B;
}
.dl-info i {
  font-size: .20rem;
  margin-right: .05rem;
}
.dot-line-list {
  position: relative;
  padding-left: .27rem;
}
.dot-line-list::before {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  width: .04rem;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: .02rem;
}
.dot-line-list li {
  position: relative;
  width: 100%;
  padding: .1rem .13rem .1rem .16rem;
  box-sizing: border-box;
  margin-bottom: .27rem;
  background: #1B418D;
  box-shadow: 0 0.03rem 0.06rem rgba(0, 0, 0, 0.16);
}
.dot-line-list li .title {
  margin-bottom: .05rem;
}
.dot-line-list li .title:before {
  left: -0.25rem;
  top: 50%;
  width: .16rem;
  height: .16rem;
  border-radius: 50%;
  background-color: #5AB0F3;
  transform: translate(-50%, -50%);
}
.dot-line-list li::before {
  left: 0;
  top: 50%;
  transform: translate(-100%, -50%);
  border: .1rem solid transparent;
  border-color: transparent rgba(0, 0, 0, 0.16) transparent transparent;
}
.dot-line-list li::after {
  left: .01rem;
  top: calc(50% - 0.01rem);
  transform: translate(-100%, -50%);
  border: .1rem solid transparent;
  border-color: transparent #1B418D transparent transparent;
}
.dot-line-list li.active,
.dot-line-list li:hover {
  background-color: #2551A2;
}
.dot-line-list li.active::after,
.dot-line-list li:hover::after {
  border-color: transparent #2551A2 transparent transparent;
}
.sort-list li {
  display: flex;
  width: 100%;
  padding: .1rem .17rem .1rem .21rem;
  box-sizing: border-box;
  background-color: #1D418C;
}
.sort-list li:nth-child(even) {
  background-color: #2953A0;
}
.sort-list li .suqare {
  display: inline-block;
  min-width: .22rem;
  height: .22rem;
  border-radius: .02rem;
  background-color: #5AB0F3;
  text-align: center;
  line-height: .22rem;
  vertical-align: top;
  margin-right: .18rem;
  margin-top: .05rem;
}
